@mixin for-mobile($min-width: 0, $max-width: 576px) {
  @media screen and (max-width: $max-width) and (min-width: $min-width) {
    @content;
  }
}
@mixin for-tablets($min-width: 576px, $max-width: 768px) {
  @media screen and (max-width: $max-width) and (min-width: $min-width) {
    @content;
  }
}
@mixin for-laptop($min-width: 768px, $max-width: 1024px) {
  @media screen and (max-width: $max-width) and (min-width: $min-width) {
    @content;
  }
}
